 (min-width: 901px) {\n  .main {\n    padding-top: 6.25rem;\n  }\n}\n\n/* ------------------------------------*    $MEDIA ELEMENTS\n\\*------------------------------------ */\n\n/**\n * Flexible Media\n */\n\niframe,\nimg,\nobject,\nsvg,\nvideo {\n  max-width: 100%;\n  border: none;\n}\n\nimg[src$=\".svg\"] {\n  width: 100%;\n}\n\npicture {\n  display: block;\n  line-height: 0;\n}\n\nfigure {\n  max-width: 100%;\n}\n\nfigure img {\n  margin-bottom: 0;\n}\n\n.fc-style,\nfigcaption {\n  font-weight: 400;\n  color: #979797;\n  font-size: 0.875rem;\n  padding-top: 0.1875rem;\n  margin-bottom: 0.3125rem;\n}\n\n.clip-svg {\n  height: 0;\n}\n\n/* ------------------------------------*    $PRINT STYLES\n\\*------------------------------------ */\n\n@media print {\n  *,\n  *::after,\n  *::before,\n  *::first-letter,\n  *::first-line {\n    background: transparent !important;\n    color: #393939 !important;\n    box-shadow: none !important;\n    text-shadow: none !important;\n  }\n\n  a,\n  a:visited {\n    text-decoration: underline;\n  }\n\n  a[href]::after {\n    content: \" (\" attr(href) \")\";\n  }\n\n  abbr[title]::after {\n    content: \" (\" attr(title) \")\";\n  }\n\n  /*\n   * Don't show links that are fragment identifiers,\n   * or use the `javascript:` pseudo protocol\n   */\n\n  a[href^=\"#\"]::after,\n  a[href^=\"javascript:\"]::after {\n    content: \"\";\n  }\n\n  blockquote,\n  pre {\n    border: 1px solid #ececec;\n    page-break-inside: avoid;\n  }\n\n  /*\n   * Printing Tables:\n   * http://css-discuss.incutio.com/wiki/Printing_Tables\n   */\n\n  thead {\n    display: table-header-group;\n  }\n\n  img,\n  tr {\n    page-break-inside: avoid;\n  }\n\n  img {\n    max-width: 100% !important;\n  }\n\n  h2,\n  h3,\n  p {\n    orphans: 3;\n    widows: 3;\n  }\n\n  h2,\n  h3 {\n    page-break-after: avoid;\n  }\n\n  #footer,\n  #header,\n  .ad,\n  .no-print {\n    display: none;\n  }\n}\n\n/* ------------------------------------*    $TABLES\n\\*------------------------------------ */\n\ntable {\n  border-collapse: collapse;\n  border-spacing: 0;\n  width: 100%;\n  table-layout: fixed;\n}\n\nth {\n  text-align: left;\n  padding: 0.9375rem;\n}\n\ntd {\n  padding: 0.9375rem;\n}\n\n/* ------------------------------------*    $TEXT ELEMENTS\n\\*------------------------------------ */\n\n/**\n * Abstracted paragraphs\n */\n\np,\nul,\nol,\ndt,\ndd,\npre {\n  font-family: \"Raleway\", sans-serif;\n  font-weight: 400;\n  font-size: 1rem;\n  line-height: 1.5rem;\n}\n\n/**\n * Bold\n */\n\nb,\nstrong {\n  font-weight: 700;\n}\n\n/**\n * Horizontal Rule\n */\n\nhr {\n  height: 1px;\n  border: none;\n  background-color: #ececec;\n  display: block;\n  margin-left: auto;\n  margin-right: auto;\n}\n\n/**\n * Abbreviation\n */\n\nabbr {\n  border-bottom: 1px dotted #ececec;\n  cursor: help;\n}\n\n/* ------------------------------------*    $LAYOUT\n\\*------------------------------------ */\n\n/* ------------------------------------*    $GRIDS\n\\*------------------------------------ */\n\n/**\n * Simple grid - keep adding more elements to the row until the max is hit\n * (based on the flex-basis for each item), then start new row.\n */\n\n.grid {\n  display: flex;\n  display: inline-flex;\n  flex-flow: row wrap;\n  margin-left: -0.625rem;\n  margin-right: -0.625rem;\n}\n\n.grid-item {\n  width: 100%;\n  box-sizing: border-box;\n  padding-left: 0.625rem;\n  padding-right: 0.625rem;\n}\n\n/**\n * Fixed Gutters\n */\n\n[class*=\"grid--\"].no-gutters {\n  margin-left: 0;\n  margin-right: 0;\n}\n\n[class*=\"grid--\"].no-gutters > .grid-item {\n  padding-left: 0;\n  padding-right: 0;\n}\n\n/**\n* 1 to 2 column grid at 50% each.\n*/\n\n.grid--50-50 > * {\n  margin-bottom: 1.25rem;\n}\n\n@media (min-width: 701px) {\n  .grid--50-50 > * {\n    width: 50%;\n    margin-bottom: 0;\n  }\n}\n\n/**\n* 1t column 30%, 2nd column 70%.\n*/\n\n.grid--30-70 {\n  width: 100%;\n  margin: 0;\n}\n\n.grid--30-70 > * {\n  margin-bottom: 1.25rem;\n  padding: 0;\n}\n\n@media (min-width: 701px) {\n  .grid--30-70 > * {\n    margin-bottom: 0;\n  }\n\n  .grid--30-70 > *:first-child {\n    width: 40%;\n    padding-left: 0;\n    padding-right: 1.25rem;\n  }\n\n  .grid--30-70 > *:last-child {\n    width: 60%;\n    padding-right: 0;\n    padding-left: 1.25rem;\n  }\n}\n\n/**\n * 3 column grid\n */\n\n.grid--3-col {\n  justify-content: center;\n}\n\n.grid--3-col > * {\n  width: 100%;\n  margin-bottom: 1.25rem;\n}\n\n@media (min-width: 501px) {\n  .grid--3-col > * {\n    width: 50%;\n  }\n}\n\n@media (min-width: 901px) {\n  .grid--3-col > * {\n    width: 33.3333%;\n    margin-bottom: 0;\n  }\n}\n\n.grid--3-col--at-small > * {\n  width: 100%;\n}\n\n@media (min-width: 501px) {\n  .grid--3-col--at-small {\n    width: 100%;\n  }\n\n  .grid--3-col--at-small > * {\n    width: 33.3333%;\n  }\n}\n\n/**\n * 4 column grid\n */\n\n.grid--4-col {\n  width: 100%;\n}\n\n@media (min-width: 701px) {\n  .grid--4-col > * {\n    width: 50%;\n  }\n}\n\n@media (min-width: 901px) {\n  .grid--4-col > * {\n    width: 25%;\n  }\n}\n\n/**\n * Full column grid\n */\n\n@media (min-width: 501px) {\n  .grid--full {\n    width: 100%;\n  }\n\n  .grid--full > * {\n    width: 50%;\n  }\n}\n\n@media (min-width: 901px) {\n  .grid--full > * {\n    width: 33.33%;\n  }\n}\n\n@media (min-width: 1101px) {\n  .grid--full > * {\n    width: 25%;\n  }\n}\n\n@media (min-width: 1301px) {\n  .grid--full > * {\n    width: 20%;\n  }\n}\n\n@media (min-width: 1501px) {\n  .grid--full > * {\n    width: 16.67%;\n  }\n}\n\n/* ------------------------------------*    $WRAPPERS & CONTAINERS\n\\*------------------------------------ */\n\n/**\n * Layout containers - keep content centered and within a maximum width. Also\n * adjusts left and right padding as the viewport widens.\n */\n\n.layout-container {\n  max-width: 81.25rem;\n  margin: 0 auto;\n  position: relative;\n  padding-left: 1.25rem;\n  padding-right: 1.25rem;\n}\n\n/**\n * Wrapping element to keep content contained and centered.\n */\n\n.wrap {\n  max-width: 81.25rem;\n  margin: 0 auto;\n}\n\n.wrap--2-col {\n  display: flex;\n  flex-direction: column;\n  flex-wrap: nowrap;\n  justify-content: flex-start;\n}\n\n@media (min-width: 1101px) {\n  .wrap--2-col {\n    flex-direction: row;\n  }\n}\n\n@media (min-width: 1101px) {\n  .wrap--2-col .shift-left {\n    width: calc(100% - 320px);\n    padding-right: 1.25rem;\n  }\n}\n\n.wrap--2-col .shift-right {\n  margin-top: 2.5rem;\n}\n\n@media (min-width: 701px) {\n  .wrap--2-col .shift-right {\n    padding-left: 10.625rem;\n  }\n}\n\n@media (min-width: 1101px) {\n  .wrap--2-col .shift-right {\n    width: 20rem;\n    padding-left: 1.25rem;\n    margin-top: 0;\n  }\n}\n\n.wrap--2-col--small {\n  display: flex;\n  flex-direction: column;\n  flex-wrap: nowrap;\n  justify-content: flex-start;\n}\n\n@media (min-width: 701px) {\n  .wrap--2-col--small {\n    flex-direction: row;\n  }\n}\n\n.wrap--2-col--small .shift-left--small {\n  width: 9.375rem;\n  flex-direction: column;\n  justify-content: flex-start;\n  align-items: center;\n  text-align: center;\n  display: none;\n}\n\n@media (min-width: 701px) {\n  .wrap--2-col--small .shift-left--small {\n    padding-right: 1.25rem;\n    display: flex;\n  }\n}\n\n.wrap--2-col--small .shift-right--small {\n  width: 100%;\n}\n\n@media (min-width: 701px) {\n  .wrap--2-col--small .shift-right--small {\n    padding-left: 1.25rem;\n    width: calc(100% - 150px);\n  }\n}\n\n/**\n * Wrapping element to keep content contained and centered at narrower widths.\n */\n\n.narrow {\n  max-width: 50rem;\n  display: block;\n  margin-left: auto;\n  margin-right: auto;\n}\n\n.narrow--xs {\n  max-width: 31.25rem;\n}\n\n.narrow--s {\n  max-width: 37.5rem;\n}\n\n.narrow--m {\n  max-width: 43.75rem;\n}\n\n.narrow--l {\n  max-width: 59.375rem;\n}\n\n.narrow--xl {\n  max-width: 68.75rem;\n}\n\n/* ------------------------------------*    $TEXT\n\\*------------------------------------ */\n\n/* ------------------------------------*    $TEXT TYPES\n\\*------------------------------------ */\n\n/**\n * Text Primary\n */\n\n.font--primary--xl,\nh1 {\n  font-size: 1.5rem;\n  line-height: 1.75rem;\n  font-family: \"Raleway\", sans-serif;\n  font-weight: 400;\n  letter-spacing: 4.5px;\n  text-transform: uppercase;\n}\n\n@media (min-width: 901px) {\n  .font--primary--xl,\n  h1 {\n    font-size: 1.875rem;\n    line-height: 2.125rem;\n  }\n}\n\n@media (min-width: 1101px) {\n  .font--primary--xl,\n  h1 {\n    font-size: 2.25rem;\n    line-height: 2.5rem;\n  }\n}\n\n.font--primary--l,\nh2 {\n  font-size: 0.875rem;\n  line-height: 1.125rem;\n  font-family: \"Raleway\", sans-serif;\n  font-weight: 500;\n  letter-spacing: 2px;\n  text-transform: uppercase;\n}\n\n@media (min-width: 901px) {\n  .font--primary--l,\n  h2 {\n    font-size: 1rem;\n    line-height: 1.25rem;\n  }\n}\n\n.font--primary--m,\nh3 {\n  font-size: 1rem;\n  line-height: 1.25rem;\n  font-family: \"Raleway\", sans-serif;\n  font-weight: 700;\n  letter-spacing: 2px;\n  text-transform: uppercase;\n}\n\n@media (min-width: 901px) {\n  .font--primary--m,\n  h3 {\n    font-size: 1.125rem;\n    line-height: 1.375rem;\n  }\n}\n\n.font--primary--s,\nh4 {\n  font-size: 0.75rem;\n  line-height: 1rem;\n  font-family: \"Raleway\", sans-serif;\n  font-weight: 500;\n  letter-spacing: 2px;\n  text-transform: uppercase;\n}\n\n@media (min-width: 901px) {\n  .font--primary--s,\n  h4 {\n    font-size: 0.875rem;\n    line-height: 1.125rem;\n  }\n}\n\n.font--primary--xs,\nh5 {\n  font-size: 0.6875rem;\n  line-height: 0.9375rem;\n  font-family: \"Raleway\", sans-serif;\n  font-weight: 700;\n  letter-spacing: 2px;\n  text-transform: uppercase;\n}\n\n/**\n * Text Secondary\n */\n\n.font--secondary--xl {\n  font-size: 5rem;\n  line-height: 1;\n  font-family: \"Bromello\", Georgia, Times, \"Times New Roman\", serif;\n  letter-spacing: normal;\n  text-transform: none;\n}\n\n@media (min-width: 901px) {\n  .font--secondary--xl {\n    font-size: 6.875rem;\n  }\n}\n\n@media (min-width: 1101px) {\n  .font--secondary--xl {\n    font-size: 8.75rem;\n  }\n}\n\n.font--secondary--l {\n  font-size: 2.5rem;\n  font-family: \"Bromello\", Georgia, Times, \"Times New Roman\", serif;\n  letter-spacing: normal;\n  text-transform: none;\n  line-height: 1.2;\n}\n\n@media (min-width: 901px) {\n  .font--secondary--l {\n    font-size: 3.125rem;\n  }\n}\n\n@media (min-width: 1101px) {\n  .font--secondary--l {\n    font-size: 3.75rem;\n  }\n}\n\n/**\n * Text Main\n */\n\n.font--l {\n  font-size: 5rem;\n  line-height: 1;\n  font-family: Georgia, Times, \"Times New Roman\", serif;\n  font-weight: 400;\n}\n\n.font--s {\n  font-size: 0.875rem;\n  line-height: 1rem;\n  font-family: Georgia, Times, \"Times New Roman\", serif;\n  font-weight: 400;\n  font-style: italic;\n}\n\n.font--sans-serif {\n  font-family: \"Helvetica\", \"Arial\", sans-serif;\n}\n\n.font--sans-serif--small {\n  font-size: 0.75rem;\n  font-weight: 400;\n}\n\n/**\n * Text Transforms\n */\n\n.text-transform--upper {\n  text-transform: uppercase;\n}\n\n.text-transform--lower {\n  text-transform: lowercase;\n}\n\n.text-transform--capitalize {\n  text-transform: capitalize;\n}\n\n/**\n * Text Decorations\n */\n\n.text-decoration--underline:hover {\n  text-decoration: underline;\n}\n\n/**\n * Font Weights\n */\n\n.font-weight--400 {\n  font-weight: 400;\n}\n\n.font-weight--500 {\n  font-weight: 500;\n}\n\n.font-weight--600 {\n  font-weight: 600;\n}\n\n.font-weight--700 {\n  font-weight: 700;\n}\n\n.font-weight--900 {\n  font-weight: 900;\n}\n\n/* ------------------------------------*    $COMPONENTS\n\\*------------------------------------ */\n\n/* ------------------------------------*    $BLOCKS\n\\*------------------------------------ */\n\n.block__post {\n  padding: 1.25rem;\n  border: 1px solid #ececec;\n  transition: all 0.25s ease;\n}\n\n.block__post:hover,\n.block__post:focus {\n  border-color: #393939;\n  color: #393939;\n}\n\n.block__latest {\n  display: flex;\n  flex-direction: column;\n}\n\n.block_